'use client';

import { useState } from 'react';
import PageHeader from '@/src/components/shared/PageHeader';
import { Card } from '@/src/components/ui/card';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/src/components/ui/select';
import { apiFetch, isAuthenticated } from '@/src/lib/api/api-client';

export default function SendMoneyPage() {
  const [amount, setAmount] = useState<string>('');
  const [currency, setCurrency] = useState<'MWK' | 'CNY'>('MWK');
  const [recipientName, setRecipientName] = useState<string>('');
  const [recipientAccount, setRecipientAccount] = useState<string>('');
  const [note, setNote] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);

  const handleSubmit = async () => {
    setLoading(true);
    setError(null);
    setSuccess(null);
    try {
      if (!isAuthenticated()) {
        setError('Please login to send money');
        return;
      }
      const payload = {
        amount: Number(amount || 0),
        currency,
        recipient_name: recipientName,
        recipient_account: recipientAccount,
        note,
      };
      const res = await apiFetch('/payments', {
        method: 'POST',
        body: JSON.stringify(payload),
      });
      if (res?.id || res?.status === 'ok') {
        setSuccess('Transfer created successfully');
        setAmount('');
        setRecipientName('');
        setRecipientAccount('');
        setNote('');
      } else {
        setError(res?.error || 'Failed to create transfer');
      }
    } catch (e: any) {
      setError(e?.message || 'Failed to create transfer');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <div className="relative overflow-hidden rounded-2xl border bg-gradient-to-br from-green-600 via-green-500 to-emerald-500">
        <div className="absolute inset-0 opacity-20 mix-blend-overlay" />
        <div className="p-6 lg:p-8 text-white">
          <h1 className="text-2xl font-semibold">Send Money</h1>
          <p className="mt-1 text-white/80">Transfer funds securely to suppliers and partners.</p>
        </div>
      </div>

      <PageHeader title="Transfer Details" subtitle="Fill in the recipient and amount." />

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <Card className="lg:col-span-2 bg-white border p-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-medium text-slate-700">Amount</label>
              <Input
                type="number"
                placeholder="0.00"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
            </div>
            <div>
              <label className="text-sm font-medium text-slate-700">Currency</label>
              <Select value={currency} onValueChange={(v) => setCurrency(v as 'MWK' | 'CNY')}>
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="Select currency" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="MWK">MWK (Malawi Kwacha)</SelectItem>
                  <SelectItem value="CNY">CNY (Chinese Yuan)</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div>
              <label className="text-sm font-medium text-slate-700">Recipient Name</label>
              <Input
                placeholder="e.g. Shenzhen Parts Factory"
                value={recipientName}
                onChange={(e) => setRecipientName(e.target.value)}
              />
            </div>
            <div>
              <label className="text-sm font-medium text-slate-700">Recipient Account</label>
              <Input
                placeholder="e.g. CN-8892001123"
                value={recipientAccount}
                onChange={(e) => setRecipientAccount(e.target.value)}
              />
            </div>
            <div className="md:col-span-2">
              <label className="text-sm font-medium text-slate-700">Note (optional)</label>
              <Input
                placeholder="Invoice #992 - steel bearings"
                value={note}
                onChange={(e) => setNote(e.target.value)}
              />
            </div>
          </div>

          <div className="mt-6 flex items-center gap-3">
            <Button onClick={handleSubmit} disabled={loading}>
              {loading ? 'Processing...' : 'Send Money'}
            </Button>
            {error && <span className="text-sm text-red-600">{error}</span>}
            {success && <span className="text-sm text-green-600">{success}</span>}
          </div>
        </Card>

        <Card className="bg-white border p-6">
          <div className="space-y-4">
            <div>
              <div className="text-sm text-slate-500">Selected Currency</div>
              <div className="text-lg font-semibold text-slate-900">{currency}</div>
            </div>
            <div>
              <div className="text-sm text-slate-500">Estimated Fees</div>
              <div className="text-lg font-semibold text-slate-900">MWK {(Number(amount || 0) * 0.01).toFixed(2)}</div>
              <div className="text-xs text-slate-500">1% standard fee</div>
            </div>
            <div>
              <div className="text-sm text-slate-500">Recipient</div>
              <div className="text-lg font-semibold text-slate-900">{recipientName || 'â€”'}</div>
              <div className="text-xs text-slate-500">{recipientAccount || ''}</div>
            </div>
          </div>
        </Card>
      </div>
    </div>
  );
}
